body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
    margin: 0 auto;
    background-color: #ecf0f1;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  margin: 0 auto;
}
.Player {
  display: flex;
  align-items: center;
  margin: 10px;
  justify-content: space-evenly;
  box-sizing: border-box;
  border: 2px solid #2980b9;
  max-width: 500px;
  background: #2980b9;
  border-radius: 50px;
  color: #fff;
}

.Player__name {
  margin: 10px;
  width: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Player__score {
  margin: 10px;
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Player__button, .Player__button_Remove {
  font-size: 20px;
  font-weight: 900;
  color: #003366;
  width: 50px;
  cursor: pointer;
  -webkit-transition: 1s linear;
  transition: 1s linear;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
}


.PlayersList {
  margin: 25px 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 40px 0 0;
}
.AddPlayer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10px;
}

.AddPlayer__input {
  border: solid 1.5px #3498db;
  border-radius: 50px;
  font-size: 20px;
  padding: 5px;
  background: #3498db;
  color: #fff;
  text-align: center;
}

.AddPlayer__input::-webkit-input-placeholder {
  color: #fff;
  text-align: center;
}

.AddPlayer__input::-ms-input-placeholder {
  color: #fff;
  text-align: center;
}

.AddPlayer__input::placeholder {
  color: #fff;
  text-align: center;
}

.AddPlayer__submit {
  color: #fff;
  text-transform: uppercase;
  background: #3498db;
  padding: 10px;
  border-radius: 50px;
  display: inline-block;
  border: none;
  margin: 5px;
  cursor: pointer;
}

.AddPlayer__submit:hover {
  text-shadow: 0px 0px 6px rgba(255, 255, 255, 1);
  box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
  -webkit-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
  -moz-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
  -webkit-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
  }
